* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans',
		'Helvetica Neue', sans-serif;
	background-color: #f1f5f8;
	color: #222;
}

.bookList {
	width: 90vw;
	max-width: 1170px;
	margin: 5rem auto;
	display: grid;
	gap: 2rem;
}

.book img {
	width: 100%;
	object-fit: cover;
}

.book {
	background: #fff;
	border-radius: 1rem;
	padding: 2rem;
	text-align: center;
}

.book h2 {
	margin-top: 1rem;
	font-size: 1rem;
}

.book h4 {
	color: #617d98;
	font-size: 0.75rem;
	margin-top: 0.5rem;
	letter-spacing: 2px;
}

@media screen and (min-width: 768px) {
	.bookList {
		grid-template-columns: repeat(3, 1fr);
	}
}
